.App {
    font-family: sans-serif;
    text-align: center;
  }
  *{
      padding: 0;
      margin: 0;
  }
  @import url("https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap");
  
  .right {
    position: absolute;
    color:'rgb(6, 19, 54)'
  }
  
  
  
  .right li a {
    display: flex;
    align-items: center;
    height: 40px;
  }
  
  .right a {
    text-align: right;
  }
  
  .right a span {
    font-size: 10px;
  }
  
  .right .dropdowncont {
    position: relative;
  }
  
  .right .dropdowncont .dropdown {
    position: absolute;
    top: 55px;
    right: -10px;
    background: #fff;
    padding: 10px 25px;
    border-radius: 5px;
    display: none;
  }
  
  .right .dropdowncont .dropdown .fas {
    margin-right: 10px;
  }
  
  .right .dropdowncont .dropdown:before {
    content: "";
    position: absolute;
    top: -20px;
    left: 80%;
    transform: translateX(-100%);
    border: 10px solid;
    border-color: transparent transparent #fff transparent;
  }
  
  .right .dropdowncont.active .dropdown {
    display: block;
  }
  