.sliderContainer{
  width:100%;
  height:100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logincontainer {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 70%;
  height:100%;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
overflow-y: auto;

}

.sign-in-container {
  left: 0;
  width: 50%;
  opacity: 1;
  z-index: 2;
}

.logincontainer.right-panel-active .sign-in-container {
  transform: translateX(100%);
  opacity: 0;
  z-index: 1;
  animation: hide 0.6s;
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.logincontainer.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}
@keyframes hide {
  0%,
  49.99% {
    opacity: 1;
    z-index: 2;
  }

  50%,
  100% {
    opacity: 0;
    z-index: 1;
  }
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.logincontainer.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.overlay {
  background-image: url(/static/media/Background.bc1ea6d5.jpeg);
  background-repeat: no-repeat;
  background-size:cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.logincontainer.right-panel-active .overlay {
  transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateX(-20%);
}

.logincontainer.right-panel-active .overlay-left {
  transform: translateX(0);
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.logincontainer.right-panel-active .overlay-right {
  transform: translateX(20%);
}
@media (max-width: 1000px){
  .logincontainer {
    width: 100%;
    height:100%;
  }
}


@media (max-width: 700px) {
  .overlay-container {
    display: none;
  }
  .logincontainer {
    overflow: hidden;
  }
  .sign-in-container,
  .sign-up-container {
    width: 100%;
  }
  .sign-up-container {
    transform: translateX(-100%);
  }
  .logincontainer.right-panel-active .sign-up-container {
    transform: translateX(0);
  }
}


@media (max-width: 500px) {
.sliderContainer{
  height: 115vh;
}

}




